header {
    .navbar {
        padding-top: 25px;
        padding-bottom: 39px;
    }

    .navbar-brand {
        padding: 0;
        margin: 0;
    }

    .navbar-light {
        .navbar-nav {
			margin-top: 3px;
            margin-left: auto;
            margin-right: auto;
			max-width: 603px;
			width: 100%;
			justify-content: space-between;

            .nav-link {
                color: #fff;
                padding: 0;
                
                &:hover {
                    color: $color-secondary;
                }
            }
        }
    }

    .btn {
        padding: 13px 22px;
    }
}
